/* RESPONSIVENESS */





@media only screen and (max-width: 1200px) {

    .fa-facebook-square,
    .fa-twitter-square,
    .fa-google-plus-square,
    .fa-instagram {
        font-size: 43px;
    }

}

@media only screen and (max-width: 992px) {
    .moto {
        font-size: 2.2rem;
    }
}


@media only screen and (max-width: 768px) {
    .moto {
        display: none;
    }

    .fa-arrow-down {
        position: absolute;
        right: 60px;
        top: 150px;
    }


     .monka-img,
    .mario-img,
    .monice-img {
        width: 65px;
        height: 65px;
    }

    .num {
        margin-left: 0;
    }

     .fa-facebook-square,
    .fa-twitter-square,
    .fa-google-plus-square,
    .fa-instagram {
        font-size: 30px;
    }

}

.social-links li {
    display: inline-block;
    margin-right: 10px;
    margin-top: 10px;
    padding-right: 15px;
}

@media only screen and (max-width: 544px) {

    .fa.fa-arrow-down {
        display: none;
    }

    br {
        display: block;
    }
}

@media only screen and (max-width: 480px) {

    .testimonial {
        margin-top: 2.5rem;
        padding-bottom: 2.5rem;
    }

    .num {
        margin-left: 0.1px;
    }

}